.Header {
    width: 90%;
    display: inline-flex;
    align-self: flex-end;
    border-bottom: 2px solid black;
    justify-content: end;

    &_item {
        border-left: 2px solid black;
        padding: 0.5rem 1rem;
    }
}


.LeftMenu {
    height: 100vh;
    width: 10%;
    border-right: 2px solid black;
    position: absolute;
    padding-top: 2.4rem;
    
    &_item {
        padding: 1rem 0 1rem 2rem;
    }
}

.Main_container {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    min-height: 100vh;
}